<template>
  <div class="play-full-footer">
    <div class="progress">
      <i>{{currentTime|filtersTime}}</i>
      <input type="range" min="0" :max="duration" step="1" @input="getValue" :value="currentTime" />
      <span :style="{ width: (currentTime / duration) * 100 + '%' }">
        <div class="dot"></div>
      </span>
      <p>{{duration|filtersTime}}</p>
    </div>
    <div class="control">
      <div class="single" v-if="singleIndex==1" @click="togglePlayingStatus">
        <img src="../assets/img/single.png" alt />
      </div>
      <div class="single" v-if="singleIndex==2" @click="togglePlayingStatus">
        <img src="../assets/img/LastSong.png" alt />
      </div>
      <div class="single" v-if="singleIndex==3" @click="togglePlayingStatus">
        <img src="../assets/img/NextSong.png" alt />
      </div>
      <div class="LastSong" @click="$emit('LastSong')">
        <img src="../assets/img/LastSong.png" alt />
      </div>
      <div class="icon" @click.stop="$emit('togglePlayState',!paused)">
        <!-- <img src="../assets/img/play_big.png" alt /> -->
        <div v-if="paused" class="iconPlay"></div>
        <div v-else class="iconpaused"></div>
      </div>
      <div class="NextSong" @click="NextSong">
        <img src="../assets/img/NextSong.png" alt />
      </div>
      <div class="list">
        <img src="../assets/img/list.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["currentTime", "duration", "paused"],
  data() {
    return {
      singleIndex: 1,
    };
  },
  filters: {
    filtersTime: function (value) {
      // return "0"+(value/60).toFixed(2);
      return (
        (parseInt(value / 60) < 10
          ? "0" + parseInt(value / 60)
          : parseInt(value / 60)) +
        ":" +
        (parseInt(value % 60) < 10
          ? "0" + parseInt(value % 60)
          : parseInt(value % 60))
      );
    },
  },
  methods: {
    getValue: function (e) {
      this.$emit("update:currentTime", e.target.value);
    },
    togglePlayingStatus: function () {
      this.singleIndex < 3 ? this.singleIndex++ : (this.singleIndex = 1);
      localStorage.setItem("singleIndex", this.singleIndex);
      this.$emit("togglePlayingStatus", this.singleIndex);
    },
    NextSong: function () {
      if (this.singleIndex == 3) {
        this.$emit("NextSongRandom");
      } else {
        this.$emit("NextSong");
        // console.log("NextSong");
      }
    },
  },
};
</script>

<style lang="less" scoped>
.play-full-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  // background: red;
  height: 18%;
  .progress {
    width: 75%;
    height: 3px;
    background: rgba(255, 255, 255, 0.5);
    position: relative;
    top: 10px;
    left: 0;
    margin: 0 auto;
    border-radius: 1px;
    input[type="range"] {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 12;
      opacity: 0;
    }
    span {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 0%;
      height: 100%;
      background: rgba(255, 255, 255, 0.9);
      border-radius: 1px;
    }
    .dot {
      width: 4px;
      height: 4px;
      background: rgba(255, 255, 255, 0.9);
      position: absolute;
      border-radius: 2px;
      z-index: 100;
      top: -0.8px;
      right: 0;
    }
    i {
      // width: 10px;
      // height: 5px;
      font-size: 10px;
      font-style: normal;
      color: #fffffb;
      position: absolute;
      top: -5px;
      left: -35px;
    }
    p {
      // width: 10px;
      // height: 5px;
      font-size: 10px;
      font-style: normal;
      color: #fffffb;
      position: absolute;
      top: -5px;
      right: -35px;
    }
  }
  .control {
    width: 100%;
    height: 80%;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    & > div {
      width: 20%;
      text-align: center;
    }
    .iconPlay {
      width: 64px;
      height: 64px;
      background-image: url("../assets/img/play_big.png");
      margin-left: 4px;
    }
    .iconpaused {
      width: 64px;
      height: 64px;
      background-image: url("../assets/img/paused_big.png");
      margin-left: 4px;
    }
  }
}
</style>