      :index="index"
      :item="song"
      :paused="$attrs.paused"
      :currentMusic="$attrs.currentMusic"
      />
    </ul>
  </section>
</template>

<script>
import BorderedTitle from "@/components/BorderedTitle.vue";
import SongItem from "@/components/SongItem.vue";
export default {
    props:{
        newSongs:{
            tyep:Array
        }
    },
  components: {
    BorderedTitle,
    SongItem,
  },
};
</script>

