<template>
  <form action="#">
    <div class="inputcover">
      <i class="srch"></i>
      <input
        type="search"
        class="input"
        v-model="something"
        @keyup.enter="getSearchKeywords"
        @input.stop="$emit('update:something',something)"
        @click="$emit('update:toggle')"
      />
      <label class="holder" v-if="!something"></label>
      <figure class="close" v-if="something" @click="toggleClose">
        <i></i>
      </figure>
    </div>
  </form>
</template>

<script>
export default {
  props: ["isenter", "som"],
  data() {
    return {
      something: "",
      searchKeywords: [],
    };
  },
  methods: {
    getSearchKeywords: function () {
      this.searchKeywords = [];
      if (!this.something) return;
      this.$root.isShowLoadingtwo = true;
      this.axios
        .get("/search?keywords=" + this.something)
        .then((response) => {
          this.axios
            .get(
              "/song/detail?ids=" +
                response.data.result.songs
                  .slice(0, 40)
                  .map((e) => e.id)
                  .join()
            )
            .then((response) => {
              response.data.songs.forEach((element) => {
                let obj = {
                  name: element.name,
                  picUrl: element.al.picUrl,
                  id: element.id,
                  song: {
                    artists: element.ar,
                  },
                  al: element.al,
                };
                this.searchKeywords.push(obj);
              });
              console.log(this.searchKeywords);
            })
            .finally(() => {
              this.$root.isShowLoadingtwo = false;
            });
        })
        .finally(() => {
          this.$root.isShowLoadingtwo = false;
        });
      this.$emit("update:searchKeywords", this.searchKeywords);
      this.$emit("update:isenter", !this.isenter);
      // console.log("getSearchKeywords");
    },
    toggleClose: function () {
      this.something = "";
      this.$emit("update:isenter", !this.isenter);
      this.$emit("update:something", this.something);
    },
  },
  watch: {
    som: function (n) {
      // console.log(n);
      this.something = n;
      this.getSearchKeywords();
    },
  },
};
</script>

<style lang="less" scoped>
form {
  padding: 15px 10px;
  .inputcover {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    box-sizing: border-box;
    background: #ebecec;
    border-radius: 30px;
    .srch {
      position: absolute;
      left: 0;
      top: 9px;
      margin: 0 8px;
      vertical-align: middle;
      width: 13px;
      height: 13px;
      background-image: url(data:image/svg+xml;base64,);
    }
    .input {
      width: 100%;
      height: 30px;
      line-height: 18px;
      background: rgba(0, 0, 0, 0);
      font-size: 14px;
      color: #333;
      border-radius: 0;
      border: 0;
      outline: none;
    }
    input::-webkit-search-cancel-button {
      display: none;
    }
    .holder {
      position: absolute;
      left: 30px;
      top: 5px;
      font-size: 14px;
      color: #c9c9c9;
      background: rgba(0, 0, 0, 0);
      pointer-events: none;
    }
    .close {
      position: absolute;
      right: 0;
      top: 0;
      width: 30px;
      height: 30px;
      line-height: 28px;
      text-align: center;
      i {
        display: inline-block;
        vertical-align: middle;
        width: 14px;
        height: 14px;
        background-image: url(data:image/svg+xml;base64,);
      }
    }
  }
}
</style>