<script >
import HomeNav from "@/components/HomeNav.vue";
import Play from "@/components/Play.vue";

export default {
  components: {
    HomeNav,
    Play,
  },
  data() {
    return {
      currentMusic: null,
      paused: null,
      playlist: [],
      currentIndex: 0,
      // a: 0,
      lyric: "",
    };
  },
  watch: {
    currentMusic: function (n) {
      // console.log(n);
      // this.$emit("update:paused", n);
      this.axios.get("/lyric?id=" + n.id).then((response) => {
        if (response.data.lrc) {
          this.lyric = response.data.lrc.lyric;
        }
        else{
          this.lyric ="[00:00.000] ~"
        }
        // console.log(response.data.lrc.lyric);
        // console.log(this.lyric);
      });
    },
  },
};
</script>

