<template>
  <div @click="$emit('toggle-show-lyric')" class="play-full-lyric">
    <ul ref="list">
      <li v-for="(item, index) in paresedLyric" :key="index">{{item.text}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["lyric", "audio", "currentTime"],
  data() {
    return {
      liHeight:30,
    }
  },
  computed: {
    //  getter
    paresedLyric: function () {
      var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
      var arr = this.lyric
        .split("\n")
        .filter((e) => e)
        .map((str) => {
          var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
          var timeArr = time.split(":");
          time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
          var text = str.replace(patt, "");
          return { time, text };
        });
      return arr;
    },
  },
  // mounted() {
  //   this.updateWords();
  // },
  methods: {
    updateWords() {
      if(this.paresedLyric[0].time!=0){
        this.paresedLyric.unshift({time: 0, text: ""})
      }
      // this.$refs.list
      if (this.$refs.list) {
          let index = this.paresedLyric.findIndex((element) => {
            return element.time > this.currentTime;
          });
          if (index === -1) {
            return;
          }
          let lis = this.$refs.list.querySelectorAll("li");
          // let liHeight = lis[index - 1].offsetHeight;
          this.$refs.list.style.marginTop =
            -this.liHeight * (index - 1) + 208 + "px";
          for (let j = 0; j < lis.length; j++) {
            lis[j].style.color = "rgb(255,255,255)";
          }
          lis[index - 1].style.color = "rgb(30,204,148)";
          // console.log(lis[index - 1].offsetHeight);
      }
    },
  },
  updated() {
    let _this = this;
    this.audio.ontimeupdate = function () {
      _this.updateWords();
    };
  },
};
</script>

<style scoped lang="less">
.play-full-lyric {
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
  /* background: white; */
  height: 65%;
  // margin-top: 20px;
  overflow: hidden;
  ul {
    margin-top: 0;
    width: 100vw;
    li {
      width: 100vw;
      min-height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 14px;
    }
  }
}
</style>