<template>
  <div class="play" :class="{ paused: paused }">
    <audio
      :src="'https://music.163.com/song/media/outer/url?id=' +
        currentMusic.id +
        '.mp3'"
      autoplay
      ref="audio"
    ></audio>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideInDown"
    >
      <div class="play-bar" v-show="isShowPlayBar" @click="isShowPlayBar=false">
        <img :src="currentMusic.picUrl" alt />
        <h5>
          <div>{{currentMusic.name}}</div>
          <span v-for="(artist, index) in currentMusic.song.artists" :key="index">
            <template>
              <template v-if="index">/</template>
              {{ artist.name }}
            </template>
          </span>
        </h5>
        <div class="control" @click.stop="togglePlayState">
          <canvas width="50" height="50" ref="circle"></canvas>
          <span class="icon"></span>
        </div>
      </div>
    </transition>
    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <div class="play-full" v-show="!isShowPlayBar">
        <div class="mask" :style="{backgroundImage: `url('${currentMusic.picUrl}')`}"></div>
        <PlayFullHeader @show-play-bar="isShowPlayBar=true" :currentMusic="currentMusic" />
        <template>
          <PlayFullLyric
            v-show="isShowLyric"
            @toggle-show-lyric="isShowLyric=!isShowLyric"
            :lyric="lyric"
            :currentTime="currentTime"
            :audio="$refs.audio"
          />
          <PlayFullChart
            v-show="!isShowLyric"
            :currentMusic="currentMusic"
            :paused="paused"
            @toggle-show-lyric="isShowLyric=!isShowLyric"
          />
        </template>
        <PlayFullFooter
          :duration="duration"
          :currentTime="currentTime"
          :paused="paused"
          @update:currentTime="$refs.audio.currentTime=$event"
          @togglePlayState="togglePlayFullFooterState"
          @NextSong="playNext"
          @NextSongRandom="playRandom"
          @LastSong="playLast"
          @togglePlayingStatus="singleIndex=$event"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import PlayFullHeader from "@/components/PlayFullHeader.vue";
import PlayFullFooter from "@/components/PlayFullFooter.vue";
import PlayFullLyric from "@/components/PlayFullLyric.vue";
import PlayFullChart from "@/components/PlayFullChart.vue";
export default {
  props: ["currentMusic", "playlist", "currentIndex", "lyric"],
  components: {
    PlayFullHeader,
    PlayFullFooter,
    PlayFullLyric,
    PlayFullChart,
  },
  data: function () {
    return {
      paused: null,
      isShowLyric: false,
      isShowPlayBar: true,
      duration: 0,
      currentTime: 0,
      gggggg: 0,
      singleIndex: 1,
      // audio :$refs.audio,
    };
  },
  mounted() {
    // console.log("mounted", this.$refs.audio);
    let audio = this.$refs.audio;
    audio.addEventListener("pause", () => {
      // console.log("");
      this.paused = true;
    });
    audio.addEventListener("play", () => {
      // console.log("");
      this.paused = false;
    });
    audio.addEventListener("durationchange", () => {
      this.duration = audio.duration;
    });
    audio.addEventListener("timeupdate", () => {
      this.currentTime = audio.currentTime;
      this.drawCircle(this.currentTime, this.duration);
    });
    audio.addEventListener("ended", () => {
      if (this.singleIndex == 1) {
        this.playNext();
      } else if (this.singleIndex == 2) {
        audio.load();
      } else if (this.singleIndex == 3) {
        this.playRandom();
      }
    });
  },
  methods: {
    drawCircle: function (n, total) {
      let canvas = this.$refs.circle;
      let ctx = canvas.getContext("2d");

      ctx.clearRect(0, 0, 50, 50);
      ctx.beginPath();
      ctx.strokeStyle = "rgba(105,105,105,0.6)";
      ctx.arc(25, 25, 18, 0, Math.PI * 2, false);
      ctx.stroke();
      ctx.closePath();

      ctx.beginPath();
      ctx.strokeStyle = "#d33a31";
      ctx.arc(
        25,
        25,
        18,
        Math.PI * -0.5,
        Math.PI * ((n / total) * 2 - 0.5),
        false
      );
      ctx.stroke();
      ctx.closePath();
    },
    togglePlayState: function () {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
    },
    togglePlayFullFooterState: function (n) {
      this.paused = n;
      this.togglePlayState();
    },
    calculateNext: function () {
      let calculateIndex;
      // if (this.singleIndex == 1) {
      if (this.currentIndex < this.playlist.length - 1) {
        calculateIndex = this.currentIndex + 1;
      } else {
        calculateIndex = 0;
      }
      return calculateIndex;
      // }
    },
    calculateLast: function () {
      let calculateIndex;
      if (this.currentIndex > 0) {
        calculateIndex = this.currentIndex - 1;
      } else {
        calculateIndex = this.playlist.length - 1;
      }
      return calculateIndex;
    },
    calculateRandom: function () {
      let calculateIndex;
      calculateIndex =
        Math.floor(Math.random() * (this.playlist.length - 1 - 0 + 1)) + 0;
      if (calculateIndex == this.currentIndex) {
        this.calculateRandom();
      } else {
        return calculateIndex;
      }
    },
    playLast: function () {
      // console.log(this.currentMusic);
      let index = this.calculateLast();
      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
    playNext: function () {
      // console.log(this.currentMusic);
      let index = this.calculateNext();
      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
    playRandom: function () {
      let index = this.calculateRandom();
      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
  },
  watch: {
    paused: function (n) {
      // console.log(n);
      this.$emit("update:paused", n);
    },
  },
};
</script>

<style scoped lang="less">
.play {
  &.paused {
    .play-bar {
      img {
        animation-play-state: paused;
      }
      .control {
        span.icon {
          &::before {
            content: " ";
            display: block;
            height: 40px;
            width: 40px;
            background: url(../assets/img/play.png) no-repeat;
            background-size: 60%;
            position: absolute;
            top: 13px;
            left: 16px;
          }
        }
      }
    }
  }
}
.play-bar {
  position: fixed;
  left: 0;
  top: calc(100vh - 54px);
  // bottom: 0;
  width: 100vw;
  background: white;
  display: flex;
  z-index: 9;
  box-shadow: 0px -1px 5px #cccccc;
  img {
    width: 36px;
    height: 36px;
    margin: 7px;
    border-radius: 50%;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
    animation: playing 6s linear infinite;
  }
  h5 {
    flex: 1;
    font-size: 14px;
    padding: 5px 0 0 5px;
    div {
      overflow: hidden;
      text-overflow: ellipsis; //
      white-space: nowrap;
      width: 17em; //px
    }
    span {
      font-size: 12px;
      color: #aaaaaa;
    }
  }
  .control {
    position: relative;
    span.icon {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      &::before {
        // content: "";
        content: " ";
        display: block;
        height: 40px;
        width: 40px;
        background: url(../assets/img/paused.png) no-repeat;
        background-size: 70%;
        position: absolute;
        top: 10px;
        left: 10.5px;
      }
    }
  }
}
.play-full {
  background: linear-gradient(
    to right,
    rgb(56, 56, 56),
    rgb(95, 95, 95),
    rgb(56, 56, 56)
  );
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  .mask {
    background: url("http://p4.music.126.net/6y-UleORITEDbvrOLV0Q8A==/5639395138885805.jpg");
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    filter: blur(30px) brightness(0.5);
  }
}
@keyframes playing {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>