<template>
  <div class="search">
    <InputCover
      @update:something="something=$event"
      @update:searchKeywords="searchKeywords=$event"
      @update:isenter="isenter=$event"
      :isenter="isenter"
      :som="something"
      @update:toggle="isenter=false"
    />
    <div v-if="!isenter">
      <SearchSuggest v-if="something" :searchSugg="searchSugg" :something="something" @update:something="something=$event"/>
      <HotSearch 
      v-else 
      :hotSearchList="hotSearchList"
      @update:something="something=$event"
       />
    </div>
    <ul v-if="isenter">
      <SearchSongItem
        @update:music="$emit('update:music', $event)"
        @update:playlist="$emit('update:playlist',searchKeywords)"
        v-for="(item, index) in searchKeywords"
        :key="index"
        :item="item"
        :index="index"
        :paused="$attrs.paused"
        :currentMusic="$attrs.currentMusic"
      />
    </ul>
  </div>
</template>

<script>
import InputCover from "@/components/InputCover.vue";
import HotSearch from "@/components/HotSearch.vue";
import SearchSuggest from "@/components/SearchSuggest.vue";
import SearchSongItem from "@/components/SearchSongItem.vue";
export default {
  data() {
    return {
      something: "",
      hotSearchList: [],
      searchSugg: [],
      timer: null,
      searchKeywords: [],
      isenter: false,
    };
  },
  components: {
    InputCover,
    HotSearch,
    SearchSuggest,
    SearchSongItem,
  },
  created() {
    this.axios.get("/search/hot").then((response) => {
      this.hotSearchList = response.data.result.hots;
      console.log(this.hotSearchList);
    });
    //
    // this.axios.get("/search/multimatch?keywords= EXID").then((response) => {
    //   response.data.resul;
    //   console.log(response);
    // });
  },
  methods: {
    getData: function () {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        if (!this.something) return;
        this.searchSugg = [];
        this.axios
          .get("/search/suggest?keywords= " + this.something + "&type=mobile")
          .then((response) => {
            this.searchSugg = response.data.result.allMatch;
            console.log(this.searchSugg);
          });
      }, 500);
    },
  },
  watch: {
    something: function () {
      this.getData();
    },
  },
};
</script>

