<template>
  <div class="play-list" v-if="playlist">
    <section class="pylst_header">
      <div class="plhead_bg" :style="{ backgroundImage: 'url('+playlist.coverImgUrl+')' }"></div>
      <div class="plhead_wrap">
        <div class="plhead_fl">
          <img class="u-img" :src="playlist.coverImgUrl" alt />
          <span class="lsthd_icon"></span>
          <i class="lsthd_num">{{playlist.playCount|formatPlayCount}}</i>
        </div>
        <div class="plhead_fr">
          <h2 class="lsthd_title">{{playlist.name}}</h2>
          <div class="lsthd_auth">
            <a href="#" class="lsthd_link">
              <div class="lsthd_ava">
                <img class="u-img" :src="playlist.creator.avatarUrl" alt />
                <span class="ava-icon"></span>
              </div>
              {{playlist.creator.nickname}}
            </a>
          </div>
        </div>
      </div>
    </section>
    <div class="pylst_list">
      <h5 class="u-smtitle"></h5>
      <ul>
        <HotSongItem
          @update:music="$emit('update:music', $event)"
          @update:playlist="$emit('update:playlist',songList)"
          v-for="(item, index) in songList"
          :key="index"
          :item="item"
          :index="index"
          :paused="$attrs.paused"
          :currentMusic="$attrs.currentMusic"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import HotSongItem from "@/components/HotSongItem.vue";
export default {
  components: {
    HotSongItem,
  },
  data() {
    return {
      playlist: null,
      songList: [],
    };
  },
  filters: {
    formatPlayCount: function (value) {
      return (value / 10000).toFixed(1) + "";
    },
  },
  activated() {
    if (this.$route.query.id) {
      this.$root.isShowLoading = true;
      this.axios
        .get("/playlist/detail?id=" + this.$route.query.id)
        .then((response) => {
          this.playlist = response.data.playlist;
          console.log(this.playlist);
          this.axios
            .get(
              "/song/detail?ids=" +
                response.data.playlist.trackIds.map((e) => e.id).join()
            )
            .then((response) => {
              response.data.songs.forEach((element) => {
                let obj = {
                  name: element.name,
                  picUrl: element.al.picUrl,
                  id: element.id,
                  song: {
                    artists: element.ar,
                  },
                  al: element.al,
                };
                this.songList.push(obj);
              });
              // console.log(this.songList);
              // console.log(response.data.songs);
            })
            .finally(() => {
              this.$root.isShowLoading = false;
            });
        })
        .finally(() => {
          this.$root.isShowLoading = false;
        });
      // console.log(this.$route.query.id);
    }
  },
};
</script>

<style scoped lang="less">
.play-list {
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  position: relative;
  .pylst_header {
    position: relative;
    padding: 30px 10px 30px 15px;
    overflow: hidden;
    display: block;
    .plhead_bg {
      // width: 100vw !important;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50%;
      -webkit-filter: blur(20px);
      filter: blur(20px);
      -webkit-transform: scale(1);
      -ms-transform: scale(1);
      transform: scale(1);
    }
    .plhead_wrap {
      display: flex;
      position: relative;
      z-index: 2;
      .plhead_fl {
        width: 126px;
        height: 126px;
        position: relative;
        background-color: #e2e2e3;
        .u-img {
          width: 100%;
          vertical-align: middle;
        }
        .lsthd_icon {
          position: absolute;
          z-index: 3;
          top: 10px;
          left: 0;
          padding: 0 8px;
          height: 17px;
          color: #fff;
          font-size: 9px;
          text-align: center;
          line-height: 17px;
          background-color: rgba(217, 48, 48, 0.8);
          border-top-right-radius: 17px;
          border-bottom-right-radius: 17px;
        }
        .lsthd_num {
          position: absolute;
          right: 2px;
          top: 0;
          z-index: 3;
          padding-left: 15px;
          color: #fff;
          font-size: 12px;
          background-position: 0;
          background-repeat: no-repeat;
          background-size: 11px 10px;
          text-shadow: 1px 0 0 rgba(0, 0, 0, 0.15);
          font-style: normal;
          background-image: url(data:image/svg+xml;base64,);
        }
        &::after {
          content: " ";
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 18px;
          z-index: 2;
          background-image: -webkit-linear-gradient(
            left,
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 0.2)
          );
          background-image: linear-gradient(
            90deg,
            rgba(0, 0, 0, 0),
            rgba(0, 0, 0, 0.2)
          );
        }
      }
      .plhead_fr {
        -webkit-box-flex: 1;
        -webkit-flex: 1 1 auto;
        -ms-flex: 1 1 auto;
        flex: 1 1 auto;
        width: 1%;
        margin-left: 16px;
        .lsthd_title {
          padding-top: 1px;
          font-size: 17px;
          line-height: 1.3;
          color: #fefefe;
          height: 44px;
          display: -webkit-box;
          -webkit-box-pack: center;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .lsthd_auth {
          display: block;
          position: relative;
          margin-top: 20px;
          text-overflow: ellipsis;
          white-space: nowrap;
          word-break: normal;
          .lsthd_link {
            text-decoration: none;
            display: inline-block;
            color: hsla(0, 0%, 100%, 0.7);
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
            text-overflow: ellipsis;
            .lsthd_ava {
              position: relative;
              display: inline-block;
              width: 30px;
              height: 30px;
              border-radius: 50%;
              vertical-align: middle;
              margin-right: 5px;
              .u-img {
                border-radius: 50%;
                width: 100%;
                vertical-align: middle;
              }
              .ava-icon {
                background-position: 0 0;
                position: absolute;
                right: -5px;
                bottom: 0;
                width: 12px;
                height: 12px;
                background-image: url(//s3.music.126.net/mobile-new/img/usericn_2x.png?6423c06=);
                background-repeat: no-repeat;
                background-size: 75px auto;
              }
            }
          }
        }
      }
    }
  }
  .pylst_list {
    .u-smtitle {
      display: block;
      height: 23px;
      line-height: 23px;
      padding: 0 10px;
      font-size: 12px;
      color: #666;
      background-color: #eeeff0;
    }
  }
}
</style>